@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:300,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Slab', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ToggleButton,.ToggleButton-disabled{
    background: white;
    border: 1px solid #ed3326; 
    font-size: calc(1px + 2vmin);
    color: #ed3326;
    text-align: center;
    font-weight: 500;
    margin: auto .25em;
    width:auto;
    padding:0 .5em;
    -webkit-user-select: none;
    -moz-user-select: none;    
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    pointer-events:auto;
    margin-bottom: 1em;
}

.ToggleButton:hover{
    color: white;
    background: #ed3326;
}

.ToggleButton-disabled{
    color: white;
    background: #ed3326;
    pointer-events:none;
}
.Toggle {
    display: flex;
    flex-direction: row;
    justify-content:left;
}
.Menu{
    display:flex;
    flex-direction: row;
    justify-content:center;
    margin-top: 1em;
}
.MenuButton,.MenuButton-disabled{
    background: white;
    border: 1px solid #ed3326; 
    font-size: calc(6px + 2vmin);
    color: #ed3326;
    text-align: center;
    font-weight: 700;
    margin: auto .25em;
    padding:0 .5em;
    -webkit-user-select: none;
    -moz-user-select: none;    
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    pointer-events:auto;
}

.MenuButton:hover{
    color: white;
    background: #ed3326;
}

.MenuButton-disabled{
    color: white;
    background: #ed3326;
    pointer-events:none;
}
.SubItem {
	display: flex;
	overflow: hidden;
	margin-top: .7em;
}

.col-left-sub,
.col-right-sub {
	text-align: left;
	flex: 1 1;
	font-weight: 300;
	font-size: calc(3px + 2vmin);
	color: black;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.col-right-sub {
	text-align: right;
	color: red;
}

.col-left-sub {
	margin-left:56px;
}
.Item,
.Item-collapse, 
.Item-expand {
	display: flex;
	overflow: hidden;
	margin-top: .5em;
	margin-bottom: .5em;
	flex-wrap: wrap;
	height:28px;
	cursor: pointer;
}

.Item-expand{
	height:auto;
	overflow:visible;
	cursor: pointer;
}

.Item {
	cursor: default;
	height:auto;
}

.col-left,
.col-left-no-icon,
.col-right {
	text-align: left;
	flex: 1 1;
	font-weight: 300;
	font-size: calc(3px + 2vmin);
	color: black;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.col-right {
	text-align: right;
	color: red;
}

.col-left {
	display: flex;
	flex:2 1;
	/* align-items: center; */
}

.col-left-no-icon{
	margin-left: 29px;
}
 
.icon-collapse,
.icon-expand {
	width: 2vh;
	height: 2vh;
	margin-right: .5em;
	background-repeat: no-repeat;
	background-size: contain;
}

.icon-collapse {
	background-image: url(../../static/media/icon-collapse.a56dc2fb.svg);
}

.icon-expand {
	background-image: url(../../static/media/icon-expand.53e9605e.svg);
}

.sub-items {
	flex:100% 1;
}

.Headers {
    display: flex;
    overflow: hidden;
}

.header-left, .header-right {
    text-align: left;
    flex: 1 1;
    font-weight: 700;
    font-size: calc(6px + 2vmin);
    color: black;
}

.header-right {
    text-align: right; 
}
.ItemsContainer{
    display:flex;
    flex-direction: column;
}
.App {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.App-header {
  text-align: center;
}

h1{
  font-weight:500;
  font-size: calc(18px + 2vmin);
  margin-top: 0;
}

h2{
  font-weight: 300;
  font-size: calc(7px + 2vmin);
  margin-top: 1em;
}

hr {
  border: 0;
  height: 1px;
  background-color: black;
  width:100%;
  margin: 2em 0;
}
